body {
  background: url(/res/good.jpg);
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
}

.App {

  margin: 0 auto;
  display: inline-block;
}
svg {
  width:100%;
  height:100%;
}
svg {
  width:auto;
  height:2000px;
  max-height:100vmin;
  max-width:100vmin;
}

.wall.active,.overhatch.active {
  fill: #2e3;
  stroke: #2e3;
  mix-blend-mode: hue;
}
.wall,.overhatch {
  mix-blend-mode: hue;
  fill: rgb(238, 201, 34);
  stroke: rgb(238, 201, 34);
}
.wall:hover,.overhatch:hover {
  fill: #f00;
  stroke: #f00;
}

.underhatch.active {
  fill: #2e3;
  stroke: #2e3;
}
.underhatch {
  opacity: 90%;
  fill: rgb(238, 201, 34);
  stroke: rgb(238, 201, 34);
}
.underhatch:hover {
  fill: #f00;
  stroke: #f00;
}

nav {
  position: absolute;
  display: flex;
  justify-content: space-between;
  width:100vmin;
}
nav > div {
  float:left;
}
nav button {
  border: 1px solid #000;
  height:7vmin;
  width:7vmin;
  font-size:3vmin;
  outline:none;
}
nav button.active {
  background-color: khaki;
}

.bomb1 {
  color: black;
  fill:   tomato;
  background-color:  tomato;
}
.bomb2 {
  color: black;
  fill:   gold;
  background-color:  gold;
}
.bomb3 {
  color: black;
  fill:   lightgreen;
  background-color:  lightgreen;
}
.bomb4 {
  color: white;
  fill:   royalblue;
  background-color:   royalblue;
}
.bombText {
  fill: black;
  
  font-family: Arial, Helvetica, sans-serif;
  font-size:20px;
}
select {
  color:white;
}
#tool-selector {
  float:right;
  height: 100%;
  margin-left:1em;
}
#tool-selector button {
  float:left;
  text-align:right;
  margin-left:1vmin;
  font-weight: 500;
  padding-top: 30%;
  padding-right:0%;
  font-size:2vmin;
  color:white;
}
#tool-selector button.active {
  border: 1px solid yellow;
}
#mira-tool {
  background-image: url(/res/mira.svg);
}
#reinforce-tool {
  background-image: url(/res/recruit-blue.svg);
}

.mira {
  fill: rgb(196, 117, 248);
  stroke: black;
  stroke-width:1;
  mix-blend-mode: hard-light;
}